import * as React from 'react';

export interface ILayoutItemProps {
    area?: string;
   
}

export const LayoutItem  = (props: React.PropsWithChildren<ILayoutItemProps>):JSX.Element => {
    const { area, children } = props;
  return (
    <>
        <div style={{gridArea: area, }}>{children}</div>
    </>
  );
};